import React from "react";
import { Card,Grid , Typography } from "@mui/material";
import "./styles/intro.css"




export default function Intro() {

    return (
        <Grid container id="intro"  
        direction="row"
        justifyContent="center"
        alignItems="center">

            <Grid item >
            <img src={window.location + "/image/IMG_20211126_100247_112 1.jpg"} alt="profile picture." id="profile-pic" />
            </Grid>
            <Grid item >
            <Card id="introCard" >
                <Typography component={"h1"} id="head" >Welcome</Typography>
                <p id="introPara" ><i>
                    <span className="parts" >" Hello,</span> <br/> <br/>
                    <span className="parts" > I am </span>  
                    <span className="parts" > M Talha. </span> <br/> <br/>
                    <span className="parts" >A website developer and I welcome you on my portfolio.</span>  <br/> <br/>
                    <span className="parts" >I am really glade to see you here, :-) "</span>
                    </i>
                </p>

            </Card>


            </Grid>
        </Grid>)


}