import { List, ListItem, ListItemAvatar, ListItemButton, ListItemText } from "@mui/material";
import React from "react";
import "./styles/mySkills.css"

let skills = [
    {
        title: "HTML5",
        icon: <i class="devicon-html5-plain"></i>
    },
    {
        title: "CSS3",
        icon: <i class="devicon-css3-plain"></i>
    },
    {
        title: "React JS",
        icon: <i class="devicon-react-original"></i>
    },
    {
        title: "MongoDB",
        icon: <i class="devicon-mongodb-plain"></i>
    },
    {
        title: "Express JS",
        icon: <i class="devicon-express-original"></i>
    },
    {
        title: "Node JS",
        icon: <i class="devicon-nodejs-plain"></i>
    }
]



export function MySkills() {

    function description(key) {
        let txt = "Hello, you clicked on " + skills[key].title;
        let i=0;
        const div = document.getElementById("descrip");
        div.innerHTML="";
        div.style.paddingTop = "0.7em";
        div.style.height = "2em";
        setTimeout (()=>{
            div.style.paddingTop = "0em";
            div.style.height = "0em";
            
        },10000)
         function writter () {
            if (i<txt.length) { 
                
                div.innerHTML += txt.charAt(i);
                i++;
                setTimeout(writter,100)
            }
        }
    
       writter();
        
    
    }
  
    return (

        <div id="main" >
            <h1 className="head" >Skills...</h1>
            <div id="descrip"></div>
            <List id="list" >
                {skills.map((skill, key) => {

                    return (
                        <ListItem id={key} className="list-item" >
                            <ListItemButton id={key + "button"} className="list-buttons" onClick={() => description(key) }>
                                <ListItemAvatar className="icon" id={key +"icon"} >{skill.icon}</ListItemAvatar>
                                <ListItemText className="text" id={key + "text"} >{skill.title}</ListItemText>
                            </ListItemButton>
                        </ListItem>
                    )
                })}
            </List>
        </div>
    )
}