import { Button, Grid, Input, List, ListItem, TextField } from "@mui/material"
import "./styles/contact.css";
import DeleteIcon from '@mui/icons-material/Delete';
import SendIcon from '@mui/icons-material/Send';
import { Box } from "@mui/system";



export function Contact() {

    let contacts = [
        {
            title: "Github",
            link: "",
            icon: <i class="devicon-github-original"></i>
        },
        {
            title: "Linkdn",
            link: "",
            icon: <i class="devicon-linkedin-plain"></i>
        },
        {
            title: "UpWork",
            link: "",
            icon: "he"
        },
        {
            title: "HackerRank",
            link: "",
            icon: "he"
        },
    ]

    return (
        <div id="contact" >
            <h1 className="Contact-head">Contact</h1>
            <Box id="contact-msg">
                    <TextField variant="filled" fullWidth multiline rows={4} name="message" placeholder="Write your message here." id="contact-msg-field" required></TextField>
                    <List className="contact-msg-button-list">
                        <ListItem>
                            <Button className="contact-msg-button" variant="outlined" color="error" size="small" startIcon={<DeleteIcon />} >Reset</Button>
                        </ListItem>
                        <ListItem>
                            <Button className="contact-msg-button" variant="outlined" color="success" size="small" endIcon={<SendIcon />}> Send</Button>
                        </ListItem>
                    </List>
            </Box>
            <Grid container id="contact-container">
                {contacts.map((value) => {
                    return (
                        <Grid item className="contact-element" >
                            <Button variant="contained" color="primary" disableElevation className="contact-button" startIcon={value.icon} >{value.title}</Button>
                        </Grid>
                    )
                })}
            </Grid>
        </div>

    )
}